import { Component, OnInit } from '@angular/core';
import {FormControl, Validators, FormGroup} from '@angular/forms';
import {AuthService} from '../../services/auth.service';
import {AdminService} from '../../services/admin.service';
import {ValidateService} from '../../services/validate.service';
import { ToastrService } from 'ngx-toastr';
import {Router} from '@angular/router';
import {University} from '../../admin.module'
import {Course} from '../../admin.module'
import {Career} from '../../admin.module'
import {Observable} from 'rxjs'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  id: String;
  name: String;
  username: String;
  password: String;
  confirmPassword: String
  email : String;
  university : String;
  course: String;
  career: String;
  skill: [String]
  universities: University[];
  courses: Course[];
  careers: Career[];
  skills: [any];

  hide = true;

  constructor(private validateService: ValidateService, 
              private authService: AuthService, 
              private adminService: AdminService,
              private toastr: ToastrService,
              private router: Router) { }

  ngOnInit() {
    this.adminService.getUniversities().subscribe( res =>{
      this.universities = res
    })
    this.adminService.getCourses().subscribe( res =>{
      this.courses = res
    })
    this.adminService.getCareers().subscribe( res =>{
      this.careers = res
    })
  }

  onCareerChange(career){
    console.log(career)
    // this.adminService.getSkills(career._id). subscribe( res => {
    //     this.skills = res
    //   })
    this.skills = career.skills
    this.career = career.name
  }


  onRegisterSubmit(){
    const user = {
      name: this.name,
      email: this.email,
      username: this.username,
      password: this.password,
      confirmPassword: this.confirmPassword,
      university: this.university,
      course: this.course,
      career: this.career,
      skills: this.skill
    }

    //Unique fields
    if (!this.validateService.validateRegister(user)){
      this.toastr.error('Please fill all the compulsory fields', 'Form Error', {timeOut: 3000})
      return false;
    }
 
    //Password Validation
    if (!this.validateService.validatePassword(user)){
      this.toastr.error('Password Mismatch', 'Form Error', {timeOut: 3000})
      return false;
    }

    // Validate email
    if (!this.validateService.validateEmail(user.email)){
      this.toastr.error('Please use a valid email', 'Form Error', {timeOut: 3000})
      return false;
    }

    // Register User
    this.authService.registerUser(user).subscribe(data => {
      if(data['success']){
        this.toastr.success('You can now login!', 'Registered Successfully', {timeOut: 3000})
        this.router.navigate(['/login'])
      }
      else {
        this.toastr.error('Username already taken', 'Registration Failed', {timeOut: 3000})
        this.router.navigate(['/register'])
      }
    })
  }

}
