import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations'
import { ToastrModule } from 'ngx-toastr';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http'
import { RouterModule, Routes } from '@angular/router'

import { MatToolbarModule, MatInputModule, MatButtonModule, MatStepperModule, MatListModule, MatGridListModule, MatSelectModule, MatMenuModule, MatIconModule, MatSidenavModule, MatFormFieldModule, MatSnackBarModule } from '@angular/material';
import { MatCardModule } from '@angular/material';
import { MatCommonModule } from '@angular/material/core';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { AdminLoginComponent } from './components/admin-login/admin-login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { UpdateProfileComponent } from './components/update-profile/update-profile.component';

import {ValidateService} from './services/validate.service'
import {AuthService} from './services/auth.service'
import {AdminService} from './services/admin.service'
import {AuthGuard} from './guards/auth.guard';

const appRoutes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'register', component: RegisterComponent},
  {path: 'login', component: LoginComponent},
  {path: 'adminlogin', component: AdminLoginComponent},
  {path: 'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path: 'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  { path: 'profile/update/:id', component: UpdateProfileComponent},
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    AdminLoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    AdminLoginComponent,
    UpdateProfileComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    ToastrModule.forRoot(),
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes),
    MatCardModule,MatSnackBarModule,
    MatToolbarModule, MatMenuModule,MatFormFieldModule,MatSelectModule,MatInputModule, MatGridListModule,
    MatCommonModule,MatButtonModule, MatListModule, MatIconModule, MatSidenavModule, MatStepperModule,
  ],
  providers: [ValidateService, AuthService, AuthGuard, AdminService],
  bootstrap: [AppComponent]
})
export class AppModule { }
