import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import {University} from '../../admin.module'
import {Course} from '../../admin.module'
import {Career} from '../../admin.module'
import {Observable} from 'rxjs'

import {MatSnackBar} from '@angular/Material';
import { AuthService } from '../../services/auth.service';
import {AdminService} from '../../services/admin.service';

@Component({
  selector: 'app-update-profile',
  templateUrl: './update-profile.component.html',
  styleUrls: ['./update-profile.component.css']
})
export class UpdateProfileComponent implements OnInit {
  id: String;
  user: any = {}
  updateForm: FormGroup
  universities: University[];
  courses: Course[];
  careers: Career[];
  skillset: [any]


  constructor(private authService: AuthService, private adminService: AdminService, private router: Router, private activatedRouter: ActivatedRoute, private snackBar: MatSnackBar, private fb: FormBuilder) {
    this.updateForm = this.fb.group({
      name: ['', Validators.required],
      email: ['', Validators.required],
      username: ['', Validators.required],
      university: ['', Validators.required],
      course:['', Validators.required],
      career: ['', Validators.required],
      skills: ['', Validators.required]
    });
   }

  ngOnInit() {
    this.activatedRouter.params.subscribe(params => {
      this.id = params.id;
      this.authService.getProfile().subscribe(profile =>{
        this.user = profile['user']
        console.log(this.user)
        this.updateForm.get('name').setValue(this.user.name);
        this.updateForm.get('email').setValue(this.user.email);
        this.updateForm.get('username').setValue(this.user.username);
        this.updateForm.get('university').setValue(this.user.university);
        this.updateForm.get('course').setValue(this.user.course);
        this.updateForm.get('career').setValue(this.user.career);
        this.updateForm.get('skills').setValue(this.user.skills);
      });
    });
     this.adminService.getUniversities().subscribe( res =>{
      this.universities = res
    })
    this.adminService.getCourses().subscribe( res =>{
      this.courses = res
    })
    this.adminService.getCareers().subscribe( res =>{
      this.careers = res
    })
  }

  onCareerChange(career){
    console.log(career)
    // this.adminService.getSkills(career._id). subscribe( res => {
    //     this.skillset = res
    //   })
    this.skillset = career.skills
  }

   updateProfile(name, email, username, university, course, career, skills){
    this.authService.updateUser(this.id, name, email, username, university, course, career, skills).subscribe(() => {
      this.snackBar.open('Profile updated successfully', 'OK', {
        duration: 4000
      });
      this.router.navigate(['/profile']);
    });
  }

}
