import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import 'rxjs/add/operator/map';
import {tokenNotExpired} from 'angular2-jwt'

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  authToken: any;
  user: any;
  isAdmin: any;

  constructor(private http:HttpClient) { }

  registerUser(user){
    let headers = new HttpHeaders({'Content-Type': 'application/json'})
    return this.http.post(`/users/register`, user, {headers:headers})
  }
  getUserById(id){
    return this.http.get(`/users/${id}`);
  }

  updateUser(id, name, email, username, university, course, career, skills){
    const user = {
      name:name,
      email: email,
      username: username,
      university: university,
      course: course,
      career: career,
      skills: skills

    };
    return this.http.post(`/users/update/${id}`, user)
  }


  authenticateUser(user){
    let headers = new HttpHeaders({'Content-Type': 'application/json'})
    return this.http.post(`/users/authenticate`, user, {headers:headers})
  }

  authenticateadminUser(user){
    let headers = new HttpHeaders({'Content-Type': 'application/json'})
    return this.http.post(`/admin-users/authenticate`, user, {headers:headers})
  }

  storeUserData(token, user){
    localStorage.setItem('id_token', token)
    localStorage.setItem('user', JSON.stringify(user))
    this.authToken = token;
    this.user = user;
    this.isAdmin = user.isAdmin
  }

  //getPofile(){
    //let headers = new HttpHeaders();
    //this.loadToken();
    //console.log('Bearer '+this.authToken)
    //headers.set('Authorization', 'Bearer '+ this.authToken).set('Content-Type', 'application/json')
    //return this.http.get(`${this.url}/users/profile`, {headers:headers})
  //}

  getProfile(){
    this.loadToken()
    let headers = new HttpHeaders({'Content-Type': 'application/json','Authorization': 'Bearer ' + this.authToken})
    return this.http.get(`/users/profile`, {headers:headers})
  }


  loadToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token
  }

  loggedIn(){
    return tokenNotExpired('id_token')
  }

  logout(){
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

}
