import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {University} from '../admin.module'
import {Course} from '../admin.module'
import {Career} from '../admin.module'
import {Skill} from '../admin.module'
import {Observable} from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class AdminService {

  constructor(private http: HttpClient) { }

  getUniversities(): Observable<University[]>{
    return this.http.get<University[]>(`/university`);
  }

  getUniversityById(id){
    return this.http.get(`/university/${id}`);
  }

  addUniversity(name){
    const university = {
        name:name
    };
    return this.http.post(`/university/add`, university)
  }

  getCourses(): Observable<Course[]>{
    return this.http.get<Course[]>(`/course`);
  }

  getCourseById(id){
    return this.http.get(`/course/${id}`);
  }

  addCourse(name){
    const course = {
        name:name
    };
    return this.http.post(`/course/add`, course)
  }

  getCareers(): Observable<Career[]>{
    return this.http.get<Career[]>(`/career`);
  }

  getCareerById(id){
    return this.http.get(`/career/${id}`);
  }

  addCareer(name){
    const career = {
        name:name
    };
    return this.http.post(`/career/add`, career)
  }

  getSkills(careerId): Observable<Skill[]>{
    return this.http.get<Career[]>(`/career/${careerId}/skill`);
  }

  getSkillById(careerId, skillId){
    return this.http.get(`/career/${careerId}/skill/${skillId}`);
  }
   
}
